<template>
<div id="navbar">
  <nav>
    <div class="nav-wrapper purple accent-4">
      <router-link to="/" class="brand-logo">
      <i class="fas fa-home fa-lg"></i>
      <span class="hide-on-small-only">Home Manager</span>
      </router-link>
      <!-- Mobile Hamburger Menu -->
      <a @click="toggleSideNav" data-target="mobile-nav" class="sidenav-trigger"><i class="material-icons">menu</i></a>
      <ul class="right hide-on-med-and-down">
        <li v-if="isLoggedIn"><router-link to="/">Dashboard</router-link></li>
        <li v-if="isLoggedIn"><router-link to="/groceries">Grocery List</router-link></li>
        <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
        <li v-if="isLoggedIn"><button class="btn black white-text" @click="logout">Log Out</button></li>
      </ul>
    </div>
  </nav>
  <!-- Mobile Side Nav -->
  
  <v-touch tag="ul" @swipeleft="toggleSideNav" id="mobile-nav" class="sidenav">
    <li v-if="isLoggedIn"><a @click.prevent="closeNav(e,'Home')">Dashboard</a></li>
    <li v-if="isLoggedIn"><a @click.prevent="closeNav(e,'GroceryList')">Grocery List</a></li>
    <li v-if="!isLoggedIn"><a @click.prevent="closeNav(e,'Login')">Login</a></li>
    <li v-if="isLoggedIn"><a @click.prevent="closeNav(e,'logout')">Logout</a></li>
  </v-touch>
  
</div>
</template>

<script>
  import firebase from 'firebase'
  import './materialize.js'

  export default {
    name: 'navbar',
    data () {
      return {
        e: null,
        isLoggedIn: false,
        currentUser: false
      }
    },
    methods: {
      // This method closes the nav and pushes the new path to the router
      closeNav: function(e, path) {
        if (path == 'logout') {
          this.logout()
        }
        // !!Jquery not working correctly for materializeCSS!!
        this.toggleSideNav()
        // $('.sidenav-overlay').trigger('click')
        this.$router.push({name: path})
      },
      logout: function() {
        firebase.auth().signOut()
          .then(() => {
            this.$router.go({path: this.$router.path})
          })
      },
      toggleSideNav() {
        console.log('clicked')
        console.log(document.querySelector('#mobile-nav'))
        console.log(document.querySelector('.sidenav-overlay'))
        document.querySelector('#mobile-nav').classList.toggle('open')
      }
    },
    created() {
      console.log(firebase.auth().currentUser)
      if (firebase.auth().currentUser) {
        this.isLoggedIn = true 
        this.currentUser = firebase.auth().currentUser.email
      }
    }
  }
</script>

<style scoped>
  ul {
    padding-right: 2rem;
  }

  .brand-logo {
      padding-left: 1rem;
    }

  .fas {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  #mobile-nav {
    transition: all .15s ease-in;
  }

  .open {
    transform: translate(0);
    transition: transform .25s ease;
  }
  
</style>




