export default {
  titleCase: function(str) {
    let strArray = str.trim().toLowerCase().split(' ')
    let titleStr = []
    strArray.forEach(word => {
      titleStr.push(word.charAt(0).toUpperCase() + word.slice(1))
    });
    return titleStr.join(' ')
  }
}


