      {{msg}} {{userFirst}}
    </h5>
      <div class="row">
        <div class="col s12 m10 offset-m1">
            <GroceryList :limit='5'/>
        </div>
      </div>
    </div>
</template>

<script>
  import GroceryList from '@/components/GroceryList'

  export default {
  name: 'Home',
  components: {
    GroceryList
  },
  data () {
    return {
      msg: 'Welcome Home',
      userFirst: 'Clive',
      userLast: 'Ward-Cron'
    }
  }
}
</script>

<style scoped>
  

</style>



