<template>
  <transition id="grocery-list">
    <ul class="collection with-header z-depth-1">
              <li class="collection-header center-align"><h5>Grocery List</h5></li>
              <!-- While the data is being retrieved, display a loader -->
              <li v-if="isLoading">
                <Loader />
              </li>
              <!-- Once the data is loaded in, dismount the loader and display data -->
              <transition-group v-else 
                name="fade"
                v-bind:css="false"
                v-on:leave="leave"
                v-on:before-enter="beforeEnter"
                v-on:enter="enter"
              >
                
                <!-- Display an area to add an item to the grocery list -->
                <li class="collection-item flow-text" key="-1">
                  <div>
                    <span v-show="isShowing">
                      <input type="text" placeholder="Quick Add" class="browser-default quick-add" id="quick-add" v-model="groceryToAdd">
                    </span>
                    <span v-show="!isShowing">
                      <select v-model="groceryToAddCat" name="category" class="browser-default" id="category">
                        <option value="null" disabled selected>Categories</option>
                        <option v-for="(category, key) in categories" :key="key" :value="key">{{category}}</option>
                      </select>
                    </span>
                    <a @click="quickAdd" class="secondary-content"><i class="material-icons small">add_box</i></a>
                  </div>
                </li>

                <!-- For each grocery list item display it in an li with the appropriate info and buttons -->
                
                <v-touch tag="li" @tap="cancelPress"
                  v-for="(item, key) in groceries" 
                  class="collection-item flow-text press" 
                  :key="item.name"
                  :data-index="key">                
                  <div>
                    <i class="fas fa-utensils fa-lg purple-text"></i>{{item.name}}
                    <a class="secondary-content">
                      <v-touch @press="onPress" @swipeleft="deleteItem">
                        <i @click="checked" class="material-icons small" v-show="!pressed">check</i>
                        <i v-show="pressed" class="material-icons small red-text">close</i>
                      </v-touch>
                    
                    </a>
                  </div>
                </v-touch>
                

                <!-- If this component is displayed with a limit set, show a "View All" link that shows all items -->
                <li v-if="this.limit" class="collection-item flow-text center" key="1000">
                  <div class="center">
                    <router-link to="/groceries" class="center-align">View All</router-link>
                  </div>
                </li>
              </transition-group>
      </ul>
  </transition>
</template>

<script>
  // Import the firebase firestore, the utility library, and the loader component
  import db from './firebaseInit'
  import util from'./utility'
  import Loader from './Loader'
  import Velocity from 'velocity-animate'
  
  // Assign the titleCase function from the utility library
  const titleCase = util.titleCase

  export default {
  name: 'GroceryList',
  components: {
    Loader
  },
  props: {
    limit: Number
  },
  data () {
    return {
      groceryToAdd: '',
      groceryToAddCat: null,
      groceries: [],
      categories: null,
      isShowing: true,
      isLoading: true,
      pressed: false
    }
  },
    methods: {
      quickAdd: function() {
        if (this.isShowing == true) {
          if (this.groceryToAdd == '') {
            return
          }
          this.isShowing = false
        } else {
          if (this.groceryToAddCat === null) {
            return
          }
          const grocery = {
              "name" : titleCase(this.groceryToAdd),
              "category" : this.groceryToAddCat,
              "quantity" : 1,
              "min" : 0
          }
          this.groceries.push(grocery)
          this.saveItem(grocery)
          this.groceryToAdd = ''
          this.isShowing = true
        }
      },
      saveItem: function(item) {
        db.collection('groceries').add({
          itemName: item.name,
          itemCategory: item.category,
          itemQuantity: item.quantity,
          itemMin: item.min
        })
      },
      deleteItem: function(e) {
        if (this.pressed) {
          // console.log(e.target.parentElement, e.target)
          let current    
          let name 
        
          if (e.target.innerText == 'close') {
            current = e.target.parentElement.parentElement.parentElement.parentElement
          } else {
            current = e.target.parentElement
          }
          name = current.innerText.split("\n")[0]
          this.groceries.forEach((item, i) => {
            if (item.name == name) {
              console.log(this.groceries[i])
              this.groceries.splice(i,1)
            }
          })
          db.collection('groceries').where('itemName', '==', name)
            .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
            })
          })
        }
      },
      checked: function(e) {
        e.target.parentElement.parentElement.parentElement.parentElement.classList.toggle('checked')
        
        e.target.classList.toggle('red-text')
        if (e.target.innerText == 'check') {
          e.target.innerText = 'undo'
        } else {
          e.target.innerText = 'check'
        }
      },
      onPress: function(e) {
        this.pressed = true
      },
      cancelPress: function(e) {
        this.pressed = false
      },
      beforeEnter: function(el) {
        el.style.opacity = 0
        el.style.height = 0
        el.style.paddingTop = 0
        el.style.paddingBottom = 0
      },
      enter: function(el, done) {
        el.style.overflow = 'hidden'
         Velocity(
          el,
          { opacity: 1, height: 'auto', paddingTop: '10px', paddingBottom: '10px' },
          { complete: done, duration: 250 }
        )
        el.style.overflow = 'inherit'
      },
      leave: function (el, done) {
          el.style.overflow = 'hidden'
          Velocity(
            el,
            { opacity: 0, height: 0, paddingTop: 0, paddingBottom: 0},
            { complete: done, duration: 400}
          )
        
      }
    },
    created () {
      let groceryLimit
      if (!this.limit) {
        groceryLimit = 1000
      } else {
        groceryLimit = this.limit
      }
      db.collection('groceries').orderBy('itemCategory').limit(groceryLimit).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              "name" : doc.data().itemName,
              "category" : doc.data().itemCategory,
              "quantity" : doc.data().itemQuantity,
              "min" : doc.data().itemMin
            }
            this.groceries.push(data)
          })
          this.isLoading = false
        }).catch(err => console.log('Error fetching grocery list data.', err))

        db.collection('categories').doc('categoryList').get().then(doc => {
          if (doc.exists) {
            this.categories = doc.data().categoryArray
          } else {
            // doc.data() will be undefined 
            console.log("The requested doc doesn't exist!")
          }
        }).catch(err => console.log('Error fetching category data.', err))
    }
}
</script>

<style scoped>
  .fas {
    padding-right: 1rem;
  }

  .checked {
    background-color: rgba(0,0,0,0.08);
  }

  .remove-li {
    transition: all .75s ease;
    transform: translateX(-125%);
  }

  #quick-add {
    width: 80%;
    height: 100%;
    margin-left: .3rem;
  }


  #category {
    display: inline-block;
    width: 80%;
    height: 30%;
    padding: .5%;
    margin-left: 1%;
    border: 1px solid #aeaeae;
  }
</style>



