<template lang="html">
  <div>
    
      <div class="row">
        <div class="col s12 m8 offset-m2">
          <div class="login card-panel purple accent-4 white-text center">
            <h2>Login</h2>
            <form>
              <div class="input-field flow-text white-text">
                <i class="material-icons prefix">email</i>
                <input class="white-text" type="text" id="email" v-model="email" autocomplete="off">
                <label class="white-text" for="email">Email</label>
              </div>
              <div class="input-field flow-text white-text">
                <i class="material-icons prefix">lock</i>
                <input class="white-text" type="password" id="password" v-model="password">
                <label class="white-text" for="password">Password</label>
              </div>
              <button @click.prevent="login" class="btn btn-large">Login</button>
            </form>
          </div>
        </div>
      </div>
    
  </div>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'login',
  data() {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    login: function(e) {
      firebase.auth().signInWithEmailAndPassword(this.email, this.password)
        .then(response => {
          this.$router.go({path: this.$router.path})
        },
      err => {
        alert(err.message);
      })
    }
  }
}
</script>

<style scoped>

  /* Imports a pretty font */
  @import url('https://fonts.googleapis.com/css?family=Courgette');

  
  button {
    font-size: 1.3rem;
  }
  /* Use the pretty font */
  /* Give some space to the login box */
  .login {
    margin-top: 10vh;
    font-family: 'Courgette', cursive;
  }

  /**********************************************
    Used to override materialize CSS input styles
  ************************************************/
  /* label color */
   .input-field label {
     color: #000;
   }
   /* label focus color */
   .input-field input[type=text]:focus + label {
     color: #000;
   }
   /* label underline focus color */
   .input-field input[type=text]:focus {
     border-bottom: 1px solid #000;
     box-shadow: 0 1px 0 0 #000;
   }
   /* valid color */
   .input-field input[type=text].valid {
     border-bottom: 1px solid #000;
     box-shadow: 0 1px 0 0 #000;
   }
   /* invalid color */
   .input-field input[type=text].invalid {
     border-bottom: 1px solid #000;
     box-shadow: 0 1px 0 0 #000;
   }
   /* icon prefix focus color */
   .input-field .prefix.active {
     color: #000;
   }
</style>




